<template>
  <div class="container">
	<my-header title="理赔申请"></my-header>
  	<section>
		<div>
			<span @click="showAlert()"><i class="fa fa-question-circle-o" aria-hidden="true"></i>申请范围</span>
		</div>
		<form>
			<div>
				<span>出险人姓名：</span>
				<cube-input @blur="RESETINPUT" v-model="username" placeholder="请输入姓名"></cube-input>
			</div>
			<div>
				<span>证件类型：</span>
				<cube-select v-model="idType" :options="claimApply.appCertiType"></cube-select>
			</div>
			<div>
				<span>证件号码：</span>
				<cube-input @blur="RESETINPUT" v-model="code" type="number" placeholder="请输入证件号码"></cube-input>
			</div>
		</form>
	</section>
	<div class="btn">
		<cube-button @click="submit()" :inline="true" :light="true">确认</cube-button>
	</div>
  </div>
</template>

<script>
import Vue from 'vue';
import MyHeader from "@/components/model/MyHeader";
import {IDREG,ISINPUTNULL} from "@/utils/publicUtils";
import {POSTAPPLYINFO} from "@/api";
import {Style,createAPI,Button,Input,Select,Dialog,DatePicker} from 'cube-ui';
import {mapState,mapMutations} from 'vuex';
Vue.use(Dialog);
Vue.use(DatePicker);
export default {
  name: 'Apply',
  data(){
  	return {
  		username:"",//许桂丽
  		idType:"0",
  		code:""//420923197908140446
  	}
  },
  mounted(){
    sessionStorage.setItem("openid",this.$route.query.openid);
  },
  computed:{
	...mapState(['claimApply','appCertiType','applicationScope'])	
  },
  methods:{
  	...mapMutations(['RESETINPUT']),
  	showAlert(){
		this.$createDialog({
			type: 'alert',
			content: this.claimApply.applicationScope,
		}).show()
	},
  	submit(){
  		ISINPUTNULL(this,this.username,"出险人姓名")&&ISINPUTNULL(this,this.code,"证件号码")&&
  		IDREG(this,this.code,"证件号码")&&this.$router.push({path:this.$route.path+"/recordreport",query:{
  			username:this.username,
  			type:this.idType,
  			code:this.code
  		}})
	}
  },
  components:{
  	MyHeader,
  	CubeSelect:Select,
  	CubeInput:Input,
  	CubeButton:Button
  }
}
</script>
<style lang="stylus" scoped>
.container{
	&>section{
		background:#fff;
		padding:0 16px 28px 16px;
		&>div{
			height:34px;
			position:relative;
			&>span{
				position:absolute;
				display: block;
				bottom:0;
				right:0
				&>.fa-question-circle-o{
					color:blue;
					font-size:22px;
					margin-right:5px;
				}
			}
		}
		&>form{
			&>div{
				width:306px;
				&>span{
					margin-bottom:14px;
					display: inline-block;
				}
				&>div{
					margin-bottom:28px;
				}
			}
		}
	}
	&>.btn{
		text-align:center;
		margin-top:60px;
		.cube-btn-light{
			background:#fd7f47;
			color:#fff;
			font-size:15px;
			padding:14px 56px;
		}
	}
}

</style>



